<template>
    <bda-dialog
        :visible="dialogVisible"
        :modal="false"
        :close-on-click-modal="false"
        :show-close="false"
        @cancel="closeDialog"
        title="历史文件及版本"
    >
        <div :class="$root.themeHomeChange === '1' ?'dialogBoxLs':'dialogBox'">
            <el-table
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :data="tableData"
                style="width: 100%"
                v-loading="fileTableLoading"
                height="320">
                <el-table-column label="文档名称" prop="documentName" >
                </el-table-column>
                <el-table-column label="版本号" prop="version" width="80">
                </el-table-column>
                <el-table-column label="上传用户" prop="uploadUser" width="120">
                </el-table-column>
                <el-table-column label="上传时间"  prop="uploadTime">
                </el-table-column>
                <el-table-column label="是否最新版本" width="130" prop="isLatest">
                    <template slot-scope="scope">
                        {{scope.row.isLatest==='1'?'是':'否'}}
                    </template>
                </el-table-column>
                <el-table-column label="说明" width="200" prop="comment">
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template slot-scope="scope">
                        <span style="margin-right: 10px; color: #66b9ff;cursor: pointer" @click="dowmFile(scope.row)">下载</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <span slot="footer" class="dialog-footer">
          <bda-button bdatype="defualt" @click="closeDialog">关闭</bda-button>
        </span>
    </bda-dialog>
</template>

<script>

  import Interface from "../../../../../base/mixin/enterprise-manage-center";
  import {
    API_PATH
  } from '@/config'
  export default {
    name: "historyDoc",
    mixins: [Interface],
    data(){
      return {
        fileTableLoading:false,
        dialogVisible:false,
        tableData:[],
        downUrl:API_PATH+'/knowledge/file/download',
      }
    },
    mounted() {
    },
    methods:{
      /**
       * @description:关闭弹框回调
       */
      closeDialog() {
        this.dialogVisible = false;
      },

      //下载历史版本数据
      getHistoryList(row){
        this.fileTableLoading = true
        this.historyVersionList({
          historyDocumentId:row.documentId
        }).then((res)=>{
          this.tableData = res
        }).finally(()=>{
          this.fileTableLoading = false
        })

      },

      //列表文件下载
      dowmFile(v){
        window.location.href = this.downUrl+'?documentId='+v.documentId+'&historyDocumentId='+v.documentHistoryId
      },
    }
  }
</script>

<style scoped lang="scss">
    /deep/ {
        /*修改添加规则弹窗 */
        .el-dialog {
            width: 1200px;
            height: 520px;
            background: #1f2c4d;
            border: 1px solid #4389f9;
            border-radius: 4px;

            .el-dialog__header {
                border-bottom: 1px solid #4389f9 !important;
                height: 48px !important;

                .bda-dialog-header .bda-dialog-header-title {
                    font-family: MicrosoftYaHei-Bold;
                    font-size: 16px !important;
                    color: #ffffff !important;
                    line-height: 48px;
                    font-weight: 700;
                }
            }

            .bda-dialog-body-main {
                height: 418px !important;
            }

            .el-dialog__footer {
                border-top: 1px solid #4389f9 !important;
                height: 53px !important;

                .dialog-footer {
                    display: block;
                    height: 100%;
                    padding: 11px 20px;
                    box-sizing: border-box;
                }
            }
        }

        .dialogBox{
            .el-table {
                padding-top: 20px;
                background-color: #1f2c4d !important;
                &::before {
                    background: #0065b3;
                }
                .has-gutter {
                    > tr {
                        > th {
                            background-color: #1f2c4d !important;
                            border-color: #0065b3;
                            .cell {
                                color: #ffffff;
                                text-align: left;
                            }
                        }
                    }
                }
                .el-table__body {
                    .el-table__row {
                        height: 52px;
                        background-color: #1f2c4d !important;
                        &:hover > td {
                            background-color: rgba(#0065b3, 0.3) !important;
                        }

                        > td {
                            border-color: #0065b3;

                            .el-radio__inner {
                                background: #041338 !important;
                                border: 1px solid #4389f9;
                            }
                            .cell {
                                color: #ffffff;
                                .el-input__inner {
                                    color: #4389f9;
                                    height: 28px;
                                    background: transparent;
                                    border-width: 0 0 1px 0 !important;
                                    border-color: #4389f9;
                                }
                            }
                        }
                    }
                }
            }
        }

        .dialogBoxLs{
            .el-table{
                padding-top: 20px;
                background-color: #ffffff !important;
                &::before {
                    background: #cccccc;
                }
                .has-gutter {
                    > tr {
                        > th{
                            background-color: #ffffff !important;
                            border-color: #cccccc;
                            .cell {
                                color: #333333;
                                text-align: left;
                            }
                        }
                    }
                }
                .el-table__body {
                    .el-table__row {
                        height: 52px;
                        background-color: #ffffff !important;
                        &:hover > td {
                            background-color: rgba(#0065b3, 0.3) !important;
                        }

                        > td {
                            border-color: #cccccc;

                            .el-radio__inner {
                                background: #041338 !important;
                                border: 1px solid #cccccc;
                            }
                            .cell {
                                color: #333333;
                                .el-input__inner {
                                    color: #4389f9;
                                    height: 28px;
                                    background: transparent;
                                    border-width: 0 0 1px 0 !important;
                                    border-color: #cccccc;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
</style>